*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

.navbar{
  background-color: transparent!important;
}

.navbar-brand{
  font-size: 1.9rem!important;
  color: #fff!important;
}

.nav_bg{
  background-color: #8e720d;
}

.navbar a{
  color: #fff!important;
  font-size: 1.3rem;
}

.navbar a:hover{
  color: #fff333!important;
}

.menu_active{
  color: #C55F00!important;
}




/* ============================================== */
/* ========== MAIN LANDING PAGE CSS ============ */
/* ============================================ */


#header{
  width: 100%;
  height: 80vh;
  /* margin-top: 70px; */
}

.home-main-div{
  margin-top: 70px;
}



#header h1{
  margin: 0 0 10px 0;
  font-size: 35px;
  font-weight: 700;
  color: #000;
}


#header .brand-name{
  color: #C55F00;
}

#header h2{
  color: #484848;
  font-size: 24px;
}

#header .get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 6px 30px;
  border-radius: 50px;
  transition: .5s;
  color:   #C55F00;
  border: 2px solid #C55F00;
  text-decoration: none;
}

#header .get-started:hover{
  background-color: #C55F00;
  color: #fff;
}

#header .header-image{
  text-align: right;
}

#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down{
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}




/* =========Contact Us Css============= */

.submit-form-btn{
  color: #8e720d;
  border: 2px solid #8e720d;
  padding: 7px 30px;
  border-radius: 50px;
}

.submit-form-btn:hover{
  color: #fff;
  background-color: #8e720d;
  transition: .5s;
}






















/* ================= Media Query ================= */


@media (max-width: 991px) {
  #header .header-image{
    text-align: center;
  }
  #header{
    text-align: center;
  }
  .about-main-div{
    margin-top: 70px;
  }
}


@media (max-width: 767px) {
  #header h1{
    font-size: 25px;
  }
  #header h2{
    font-size: 20px;
  }
}